//media query values
$small: 43.813em; //701px
$smallest: 43.750em;//700px
$big: 75.000em; //1200px
//media query mixin
// if/else
@mixin mq($breakpoint: $small){
  @if $breakpoint == $small {
    @media (min-width: $small) {
      @content;
    }
  }
     @else if $breakpoint == $smallest{
    @media (max-width: $smallest) {
      @content;
    }
  }
  @else {
    @media (min-width: $big) {
      @content;
    }
  }
} 